.Drawer{
    position: fixed;
    right: 0;
    bottom: 0;
    top: 0;
    box-sizing: border-box;
    width: 80%;
    max-width: 300px;
    background: #fff;
    padding: 10px 20px;
    transform: translateX(0px);
    transition: transform .3s ease;
    z-index: 90;
}
.Drawer ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
.Drawer ul li{
    margin-bottom: 15px;
}
.Drawer ul li a{
    font-size: 30px;
    color: #363d54;
    text-decoration: none;
    position: relative;
}
.Drawer ul li a:hover{
    opacity: .7;
}
.Drawer.close{
    transform: translateX(300px);
}